import React from "react";
import "./styles.css";
import { useState } from "react";
const moviesObj = {
  drama: [
    { name: "Ee Nagaraniki Emaindi", rating: "8/10" },
    { name: "Bangalore Days", rating: "8.3/10" }
  ],
  crime: [
    { name: "Joseph", rating: "8.1/10" },
    { name: "Vikram Vedha", rating: "8.5/10" }
  ],
  inspiration: [
    {
      name: "The Pursuit of Happyness",
      rating: "8/10"
    },
    {
      name: "The Founder",
      rating: "7.3/10"
    }
  ]
};
const moviesDB = Object.keys(moviesObj);

export default function App() {
  const [selectedGenre, setGenre] = useState("drama");

  function clickHandler(genre) {
    setGenre(genre);
  }

  return (
    <div className="container">
      <h1>ðŸ“š goodMovies</h1>
      <p>Checkout my favorite books. Select a genre to get started</p>
      {moviesDB.map((genre) => {
        return <button onClick={() => clickHandler(genre)}>{genre}</button>;
      })}
      <hr></hr>

      <div>
        <ul>
          {moviesDB[selectedGenre].map((film) => {
            <li key={film.name}>
              <div> {film.name} </div>
              <div> {film.rating} </div>
            </li>;
          })}
        </ul>
      </div>
    </div>
  );
}
